import React, {Component} from 'react';
import { Link, Redirect} from 'react-router-dom';
import firebase from '../../firebase.js';

import './admin-page.sass';
import SearchPanel from "../search-panel";

import { logout } from '../../utils';
import Modal from "react-modal";
import EditModal from "../edit-modal";
import { withTranslation } from 'react-i18next';

Modal.setAppElement('#root');

class AdminPage extends Component {

  constructor() {
    super();
    this.state = {
      id: 1,
      code: '',
      title: '',
      category: 'smartphones',
      brand: '',
      price: '',
      option: {
        optionId: 0,
        color: 'gold',
        pictureUrl: ''
      },
      options: [],
      items: [],
      term: '',
      redirect: false,
      editModalIsOpen: false,
      editModalCode: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChangeOption = this.handleChangeOption.bind(this);
    this.addOption = this.addOption.bind(this);
    this.openEditModal = this.openEditModal.bind(this);
    this.closeEditModal = this.closeEditModal.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name] : e.target.value
    });
  }

  handleChangeOption(e) {
    this.setState({
      option: {
        ...this.state.option,
        [e.target.name] : e.target.value
      }
    });
  }

  addOption = () => {
    if (this.state.option.pictureUrl !== '') {
      this.setState(state => {
        const options = [...state.options, state.option];
        return {
          options,
          option: {
            optionId: options.length,
            color: 'gold',
            pictureUrl: ''
          },
        };
      });
    } else {
      alert(this.props.t('fill-in'));
    }
    return [...this.state.options, this.state.option];
  };

  handleSubmit(e) {
    e.preventDefault();
    const options = this.addOption();
    const id = this.state.items.length + 1;
    const brand = this.state.brand.toLowerCase();
    const itemsRef = firebase.database().ref('items');
    const item = {
      id: id,
      code: this.state.code,
      title: this.state.title,
      category: this.state.category,
      brand: brand,
      price: Number(this.state.price),
      options: options
    }
    itemsRef.push(item);
    this.setState({
      id: id,
      code: '',
      title: '',
      category: 'smartphones',
      brand: '',
      price: '',
      option: {
        optionId: 0,
        color: '',
        pictureUrl: ''
      },
      options: []
    });
    alert(this.props.t('item-added'));
  }

  componentDidMount() {
    const itemsRef = firebase.database().ref('items');
    itemsRef.on('value', (snapshot) => {
      let items = snapshot.val();
      let newState = [];
      for (let item in items) {
        newState.push({
          id: items[item].id,
          code: items[item].code,
          title: items[item].title,
          category: items[item].category,
          brand: items[item].brand,
          price: items[item].price,
          options: items[item].options
        });
      }
      this.setState({
        items: newState
      });
    });
  }

  removeItem(itemId) {
    const rootRef = firebase.database().ref('items');
    const codeRef = rootRef.orderByChild("id").equalTo(Number.parseInt(itemId, 10));
    let parent;
    codeRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        parent = childSnapshot.key;
      });
    });
    firebase.database().ref('items').child(parent).remove();
  }

  onSearchChange = (term) => {
    this.setState({ term });
  };

  search(items, term) {
    if (!term) return items;
    return items.filter((item) => {
      return item.title.toLowerCase().indexOf(term.toLowerCase()) > -1;
    });
  }

  handleLogout = () => {
    logout();
    this.setState({
      redirect: true
    })
  };

  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to='/' />
    }
  };

  openEditModal(id) {
    this.setState({
      editModalIsOpen: true,
      editModalCode: id
    });
  }

  closeEditModal() {
    this.setState({editModalIsOpen: false});
  }



  render() {

    const { items, term, options, option } = this.state;

    const { t } = this.props;

    const visibleItems = this.search(items, term);

    return (
      <div className="page-wrapper">
        <main className="page admin-content">
          <div className="shop-content">
            <button className="btn-clear admin-home">
              <Link to="/">{t('to-home')}</Link>
            </button>

              {this.renderRedirect()}
              <button className="btn-clear admin-logout" onClick={() => this.handleLogout()}>{t('logout')}</button>


            <form className="fast-buy add-item" onSubmit={this.handleSubmit}>
              <div className="center">
                <span>{t('input-name')}</span>
                <input type="text" placeholder="Macbook Pro 15" name="title" required onChange={this.handleChange} value={String(this.state.title)}/>
                <span>{t('input-price')}</span>
                <input type="number" placeholder="39999" name="price" required onChange={this.handleChange} value={String(this.state.price)}/>
                <span>{t('input-code')}</span>
                <input type="text" placeholder="31003" name="code" required onChange={this.handleChange} value={this.state.code} />
                <span>{t('select-category')}</span>
                <select name="category" value={this.state.category} onChange={this.handleChange} >
                  <option value="smartphones">{t('smartphones')}</option>
                  <option value="notebooks">{t('notebooks')}</option>
                  <option value="watches">{t('smart-watches')}</option>
                  <option value="audio">{t('audio')}</option>
                  <option value="fitness">{t('fitness')}</option>
                  <option value="tv">{t('tv')}</option>
                  <option value="accessories">{t('accessories')}</option>
                </select>
                <span>{t('input-brand')}</span>
                <input type="text" placeholder="Samsung" name="brand" required onChange={this.handleChange} value={this.state.brand} />

                  <span>{t('options')}</span>
                  <ul className="options-list">
                  {options.map(item =>
                    <li key={item.optionId}>
                      <div className={`box ${item.color}`}></div>
                      <span>{item.color}</span>
                    </li>
                  )}
                  </ul>
                  <span>{t('select-color')}</span>
                  <select name="color" value={option.color} onChange={this.handleChangeOption} >
                    <option value="gold" className="gold">gold</option>
                    <option value="spacegray" className="spacegray">spacegray</option>
                    <option value="silver" className="silver">silver</option>
                    <option value="white" className="white">white</option>
                    <option value="black" className="black">black</option>
                    <option value="blue" className="blue">blue</option>
                    <option value="yellow" className="yellow">yellow</option>
                    <option value="coral" className="coral">coral</option>
                    <option value="red" className="red">red</option>
                  </select>
                  <span>{t('input-image')}</span>
                  <input type="text"
                         placeholder="https://pictureurl.jpg"
                         name="pictureUrl"
                         required
                         value={option.pictureUrl}
                         onChange={this.handleChangeOption} />
                  <div className="option-add" onClick={this.addOption}>{t('add-option')}</div>

                <button className="buy-now btn-clear btn-add">{t('add-item')}</button>
              </div>
            </form>
          </div>

          <section className='display-items'>
            <div className="admin-search">
              <SearchPanel onSearchChange={this.onSearchChange}/>
            </div>
            <div className="shop-content">

              {visibleItems.slice(0).reverse().map((item) => {
                return (
                  <li key={item.id} className="cart-item admin-item">
                    <div className="preview-img">
                      <img
                        src={item.options[0].pictureUrl}
                        alt="product"/>
                    </div>
                    <div className="preview-body">
                      <div className="preview-text">
                        <Link to={'/item-page/' + item.code}>{item.title}</Link>
                      </div>
                      <span className="preview-price">{item.price} {t('uah')}</span>
                      <span className="preview-code">{t('code')}: {item.code}</span>
                      <ul className="options-list preview-options">
                        {item.options.map((option, id) =>
                          <li key={id}>
                            <div className={`box ${option.color}`}/>
                          </li>
                        )}
                      </ul>
                      <div className="preview-info-wrapper">
                        <i className="preview-info"/>
                        <div className="preview-info-hover">
                          <span>{item.category}</span>
                          <span>{item.brand}</span>
                        </div>
                      </div>
                      <div className="admin-actions">
                        <i className="admin-edit" onClick={() => this.openEditModal(item.id)}/>
                        <i className="admin-delete"  onClick={() => {if (window.confirm(t('on-delete'))) this.removeItem(item.id) }}/>
                      </div>
                    </div>
                  </li>
                )
              })}

            </div>
           </section>

          <Modal
            isOpen={this.state.editModalIsOpen}
            onRequestClose={this.closeEditModal}
            className="modal-window"
            contentLabel="Edit Modal">
            <button className="modal-close btn-clear" onClick={this.closeEditModal}/>
            <h2>{t('editing')}</h2>
            <EditModal id={this.state.editModalCode} />
          </Modal>

        </main>
      </div>
    );
  }
}

export default withTranslation()(AdminPage)
