import React, {Component} from "react";
import firebase from "../../firebase";
import { withTranslation } from 'react-i18next';

import './edit-modal.sass'


class EditModal extends Component {

  constructor() {
    super();
    this.state = {
      code: '',
      title: '',
      category: 'smartphones',
      brand: '',
      price: '',
      option: {
        optionId: 0,
        color: 'gold',
        pictureUrl: ''
      },
      options: [],
      onAdded: false
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleAddOption = this.handleAddOption.bind(this);
    this.deleteOption = this.deleteOption.bind(this);
    this.addOption = this.addOption.bind(this);
  }


  componentDidMount() {
    const rootRef = firebase.database().ref('items');
    const idRef = rootRef.orderByChild("id").equalTo(Number.parseInt(this.props.id, 10));

    idRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        const item = childSnapshot.val();
        this.setState({
          code: item.code,
          title: item.title,
          category: item.category,
          brand: item.brand,
          price: item.price,
          options: item.options
        });
      });
    });
  }

  handleChange(e) {
    this.setState({
      [e.target.name] : e.target.value
    });
  }

  handleChangeOption(idx, e) {
      const arr = this.state.options;

      const oldItem = arr[idx];
      const newItem = {
        ...oldItem,
        [e.target.name] : e.target.value
      };

      const newArray = [...arr.slice(0, idx), newItem, ...arr.slice(idx + 1)];
      this.setState({
        options: newArray
      });
  }

  handleAddOption(e) {
    this.setState({
      option: {
        ...this.state.option,
        [e.target.name] : e.target.value,
        optionId: this.state.options.length
      }
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    const { code, title, category, price } = this.state;
    const brand = this.state.brand.toLowerCase();
    const options = this.addOption(true);

    const rootRef = firebase.database().ref('items');
    const idRef = rootRef.orderByChild("id").equalTo(Number.parseInt(this.props.id, 10));

    let parent;
    idRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        parent = childSnapshot.key;
      });
    });

    rootRef.child(parent).update({
      title: title,
      code: code,
      brand: brand,
      category: category,
      price: price,
      options: options
    }).then(() => rootRef.once('value'))
      .then(snapshot => snapshot.val());

    alert(this.props.t('up-to-date'));
  }

  addOption = (fromSubmit) => {
    if (!this.state.onAdded && !fromSubmit) {
      this.setState({onAdded: !this.state.onAdded});
      return this.state.options
    } else if (!this.state.onAdded && fromSubmit) {
      return this.state.options
    } else {
      if (this.state.option.pictureUrl !== '') {
        this.setState(state => {
          const options = [...state.options, state.option];
          return {
            options,
            option: {
              optionId: options.length,
              color: 'gold',
              pictureUrl: ''
            },
            onAdded: !state.onAdded
          };
        });
      } else {
        alert(this.props.t('fill-in'));
      }
      return [...this.state.options, this.state.option];
    }

  };

  deleteOption(idx) {
    this.setState(({ options }) => {
      const before = options.slice(0, idx);
      const after = options.slice(idx + 1);

      const newArray = [ ...before, ...after];
      return {
        options: newArray
      }
    })
  }

  render() {

    const { code, title, category, brand, price, options, option } = this.state;

    const { t } = this.props;

    return (
      <div className="edit-modal">
        <form className="fast-buy add-item edit-form" onSubmit={this.handleSubmit}>
          <div className="center">
            <span>{t('title')}</span>
            <input type="text" name="title" onChange={this.handleChange} value={String(title)}/>
            <span>{t('price')}({t('uah')})</span>
            <input type="number" name="price" onChange={this.handleChange} value={String(price)}/>
            <span>{t('code')}</span>
            <input type="text" name="code" onChange={this.handleChange} value={code} />
            <span>{t('category')}</span>
            <select name="category" value={category} onChange={this.handleChange} >
              <option value="smartphones">{t('smartphones')}</option>
              <option value="notebooks">{t('notebooks')}</option>
              <option value="watches">{t('smart-watches')}</option>
              <option value="audio">{t('audio')}</option>
              <option value="fitness">{t('fitness')}</option>
              <option value="tv">{t('tv')}</option>
              <option value="accessories">{t('accessories')}</option>
            </select>
            <span>{t('brand')}</span>
            <input type="text" name="brand" onChange={this.handleChange} value={brand} />

            <span>{t('options')}</span>
            <ul className="options-list">
              {options.map((item, idx) =>
                <li key={idx}>
                  <div className="preview-img">
                    <img
                      src={item.pictureUrl}
                      alt="item"/>
                  </div>
                  <input  type="text"
                          name="pictureUrl"
                          value={item.pictureUrl}
                          onChange={(e) => this.handleChangeOption(idx, e)} />
                  <div className={`box ${item.color}`}/>
                  <select name="color" value={item.color} onChange={(e) => this.handleChangeOption(idx, e)} >
                    <option value="gold" className="gold">gold</option>
                    <option value="spacegray" className="spacegray">spacegray</option>
                    <option value="silver" className="silver">silver</option>
                    <option value="white" className="white">white</option>
                    <option value="black" className="black">black</option>
                    <option value="blue" className="blue">blue</option>
                    <option value="yellow" className="yellow">yellow</option>
                    <option value="coral" className="coral">coral</option>
                    <option value="red" className="red">red</option>
                  </select>
                  <span hidden={idx === 0} className="delete-option"
                        onClick={() => {if (window.confirm(t('on-delete-option'))) this.deleteOption(idx) }}/>
                </li>
              )}
            </ul>
            <div className="option-add" onClick={() => this.addOption(false)}>{t('add-option')}</div>

            <div hidden={!this.state.onAdded}>
              <span>{t('input-color')}</span>
              <select name="color" value={option.color} onChange={this.handleAddOption} >
                <option value="gold" className="gold">gold</option>
                <option value="spacegray" className="spacegray">spacegray</option>
                <option value="silver" className="silver">silver</option>
                <option value="white" className="white">white</option>
                <option value="black" className="black">black</option>
                <option value="blue" className="blue">blue</option>
                <option value="yellow" className="yellow">yellow</option>
                <option value="coral" className="coral">coral</option>
                <option value="red" className="red">red</option>
              </select>
              <span>{t('input-image')}</span>
              <input type="text"
                     placeholder="https://pictureurl.jpg"
                     name="pictureUrl"
                     value={option.pictureUrl}
                     onChange={this.handleAddOption} />
            </div>

            <button className="buy-now btn-clear btn-add">{t('update-data')}</button>
          </div>
        </form>
      </div>
    );
  }
}

export default withTranslation()(EditModal)
