import React, { Component } from 'react';
import { login } from '../../utils';
import { withTranslation } from 'react-i18next';

import './signin.sass';

class SignIn extends Component {

  constructor() {
    super();
    this.state = {
      password: ''
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleLogin = this.handleLogin.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name] : e.target.value
    });
  }

  handleLogin(e) {
    e.preventDefault();
    if (this.state.password === 'r1music') {
      login();
      this.props.history.push('/admin-page');
    } else {
      alert(this.props.t('right-pass'));
    }
  };

  render() {

    const { t } = this.props;

    return (
      <div className="signin-page">
        <form className="fast-buy">
          <h2>{t('auth')}</h2>
          <input type="password" placeholder={t('input-password')} name="password" onChange={this.handleChange} value={this.state.password} />
          <button className="btn-clear auth-btn" onClick={this.handleLogin}>{t('login')}</button>
        </form>
      </div>
    );
  }

};

export default withTranslation()(SignIn)
