import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {withTranslation} from "react-i18next";

import './categories.sass';

class Categories extends Component {

  render() {

    const { t } = this.props;

    return (
        <aside className="categories">
          <nav>
            <ul>
              <li><Link to="/shop-page/smartphones" className="category">{t('smartphones')}</Link></li>
              <li><Link to="/shop-page/notebooks" className="category">{t('notebooks')}</Link></li>
              <li><Link to="/shop-page/watches" className="category">{t('smart-watches')}</Link></li>
              <li><Link to="/shop-page/audio" className="category">{t('audio')}</Link></li>
              <li><Link to="/shop-page/fitness" className="category">{t('fitness')}</Link></li>
              <li><Link to="/shop-page/tv" className="category">{t('tv')}</Link></li>
              <li><Link to="/shop-page/accessories" className="category">{t('accessories')}</Link></li>
            </ul>
          </nav>
        </aside>
    )
  }
}

export default withTranslation()(Categories);