import React, {Component} from 'react';

import './selected.sass';
import {withTranslation} from "react-i18next";


class Selected extends Component{

  render() {

     const { selectedRange, maxPrice, selectedBrand, selectedMemory, t } = this.props;

    return (
      <aside className="sidebar">
        <div className="selected-header">
          <h3>{t('youve-selected')}:</h3>
            {(selectedRange || selectedBrand || selectedMemory) && (
                <button className="btn-clear delete-all-selected" onClick={() => {this.props.removeSelectedRange(Math.floor(1000 + Math.random() * 9000))}}>
                    <span>{t('clear-all')}</span>
                </button>
            )}
        </div>

          {maxPrice && (
              <div className="selected-item">
                  <span>{t('price')}:</span>
                  <span>{t('from')} 0 {t('to')} {maxPrice}</span>
              </div>
          )}

          {selectedRange && (
              <div className="selected-item">
                  <span>{t('price')}:</span>
                  <span>{t('from')} {selectedRange.min} {t('to')} {selectedRange.max}</span>
                  <button className="btn-clear delete-selected" onClick={() => {this.props.removeSelectedRange(Math.floor(1000 + Math.random() * 9000))}}/>
              </div>
          )}

          {selectedBrand && (
              <div className="selected-item">
                  <span>{t('brand')}:</span>
                  <span>Apple</span>
                  <button className="btn-clear delete-selected"/>
              </div>
          )}

          {selectedMemory && (
              <div className="selected-item">
                  <span>Объем памяти: </span>
                  <span>512 Гб</span>
                  <button className="btn-clear delete-selected"/>
              </div>
          )}

      </aside>
    );
  }
}

export default withTranslation()(Selected);
